import { render, Suspense } from '@wordpress/element';
import { getRgbaColorObject } from './utilities';
import SnowingCanvas from './components/SnowingCanvas';

window.addEventListener( 'DOMContentLoaded', () => {
	const element = document.querySelector(
		'.wp-block-wallpaper-holiday-snow .wallpaper-canvas'
	);

	if ( element ) {
		const snowColor = getRgbaColorObject(
			element.getAttribute( 'data-snow-color-value' )
		);

		render(
			<Suspense fallback={ <div className="wp-block-placeholder" /> }>
				<SnowingCanvas snowColor={ snowColor } />
			</Suspense>,
			element
		);
	}
} );
